@import url('https://fonts.googleapis.com/css2?family=Praise&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Oswald&display=swap');
  *{



      transition: all 0.1s ease-in;
      
      transition: color 0.2 ease-in;


  }
  .post {

    font-family: Arial, Helvetica, sans-serif;
    text-align: center;
    
  border: rgb(250, 152, 39) solid 2px;
  border-radius: 4px;
  margin: 10px;
  margin-top: 130px;
  width: 20%;
  padding: 20px;
  background-color: rgb(255, 245, 225);
  min-width: 20%;
  max-width: 20%;

  
  }
.postBig{
  min-width: 100%;
  max-width: 100%;
  text-align: left;
  
  }

  h2 {
      text-align: center;
  }

  h2,  .post h3 {
    margin-top: 0px;
      margin-bottom: 0px;
       font-family: 'Oswald', sans-serif;
      font-size: 1.8em;
      color:  rgb(226, 121, 0)
  }

  .post h3 {
    font-size: 1.5em;
  }

  .post ul li{
    list-style-type: circle;
    margin-bottom: 15px; }

    .post ol li{
        margin-bottom: 15px; }
    

    .post ul li::marker{
        color: rgb(226, 121, 0);
    }

  .post p {
      text-align: left;
      font-family: Arial, Helvetica, sans-serif;
      font-size: 1.1em;
      line-height: 140%;  


  }

  .post ul, .post ol {
    margin-left: -1.2em;  
  }

  .hidden{
      height: 0px;
      overflow: hidden;
  }

  .post img{
      width: 235px;
      border-radius: 50%;
      height: 235px;
      object-fit: cover;  
      border: 8px solid rgb(255, 238, 200);
      display: block;
      margin: 0 auto;
      
      margin-top: -135px;

      
      
  }

  @media only screen and (max-width: 599px) {
  }




@media only screen and (max-width: 1500px ) {
	.post img{
        width: 100%;
        border-radius: 5px;
        height: 235px;
        object-fit: cover;  
        border: none;
        display: block;
        margin: 0 auto;
        
        margin-top:0 ;         
        
    }

    .post {

        font-family: Arial, Helvetica, sans-serif;
        text-align: left;
        
      border: rgb(250, 152, 39) solid 2px;
      border-radius: 4px;
      margin: 10px;
      margin-top: 30px;
      width: 20vw;
      padding: 20px;
      background-color: rgb(255, 245, 225);
      font-family:Arial, Helvetica, sans-serif;
      min-width: 40%;
      max-width: 40%;
    
      
      }
}

@media only screen and (max-width: 980px ) {
    .post img{
        width: 100%;
        border-radius: 5px;
        height: 235px;
        object-fit: cover;  
        border: none;
        display: block;
        margin: 0 auto;
        
        margin-top:0 ;         
        
    }

    

    .post {

        font-family: Arial, Helvetica, sans-serif;
        text-align: left;
        
      border: rgb(250, 152, 39) solid 2px;
      border-radius: 4px;
      margin: 10px;
      margin-top: 30px;
      width: 20vw;
      padding: 20px;
      background-color: rgb(255, 245, 225);
      font-family:Arial, Helvetica, sans-serif;
      min-width: 90%;
      max-width:90%;
    
      
      }
	
}

.postBig{
    min-width: 100%;
    max-width: 100%;
  
    
    
  }

  
.FaUtensils {
  font-size: 24pt;
  border-left: auto;
}

